*{
  box-sizing: border-box;
}
body{
  background-color: rgb(101, 165, 42);
}
.card{
    display: flex;
    width: 300px;
    height: 90px;
    display:flexbox;
    flex-wrap: wrap;
    align-items: center;
    margin: 30px 600px;
    border: 3px solid black;
    border-radius: 20px;
    box-shadow: 1px 1px 1px 1px rgba(0,0,0,.5);

  }
  img{

    width: 40%;
    padding: 20px;
  }